document.addEventListener("DOMContentLoaded", () => {
	
  const modal = document.querySelector("#messengers_modal_popup");
  
  const modalOpenChatButton = document.querySelector(
    "#messengers_modal_popup #liveChatButton"
  );

  if (modal) {
    modalOpenChatButton.onclick = function () {
      let clsBtn = document.querySelector(".live-support_btn");
      if (clsBtn) {
        // kludge to close the popup. To implement through bootstrap in design system
        clsBtn.click();
      }
      onZopimChatOpen();
    };

    modal.addEventListener("show.bs.modal", () => {
      if (!window._liveChatScriptState) {
        loadScriptsForChatBot();
      }
    });
  }
});

const loadScriptsForChatBot = (proactive) => {
  const country = getBotCountryCode();
  const language = getBotLang();

  // TODO pass from global var (SITE.dir)
  const position = "br";
  // TODO pass from global var (SITE.version)
  const version = "global";

  if (aivoIsAvailable(country) && language !== "zh") {
    return setTimeout(() => {
      registerTokenForExternalZendeskFunc(country);
      zEInitialSettings();
    });
  } else if (zEIsAvailable(country)) {
    return loadZendesk(proactive, language, version, country, position);
  }

  return "";
};

const openChatBot = (proactive) => {
  const country = getBotCountryCode();
  const language = getBotLang();

  // TODO pass from global var (SITE.dir)
  const position = "br";
  // TODO pass from global var (SITE.version)
  const version = "global";

  if (!window._liveChatScriptState) {
    loadScriptsForChatBot(proactive);
  }
  if (window._liveChatScriptState != CHAT_SCRIPT_READY) {
    setTimeout(() => {
      if (aivoIsAvailable(country)) {
        return setTimeout(() => {
          registerTokenForExternalZendeskFunc(country);
        });
      } else if (zEIsAvailable(country)) {
        return openzE(departments_config, country, language);
      }
    }, 1000);
    return;
  }

  if (aivoIsAvailable(country)) {
    return setTimeout(() => {
      registerTokenForExternalZendeskFunc(country);
    });
  } else if (zEIsAvailable(country)) {
    return openzE(departments_config, country, language);
  }
};

const aivoIsAvailable = (country) => aivoCountries.includes(country);

const loadAivo = (country) => {
  let src = "";
  aivoStaticFilesByRegionMapping.forEach((item) => {
    if (item.countries.includes(country)) {
      src = "https://cdn.agentbot.net/core/" + item.token + ".js";
    }
  });
  getScript(src);
};

const registerTokenForExternalZendeskFunc = (country) => {
  if (!window.$aivo) {
    loadAivo(country);
  }
  openAivoWhenReady();

  let token = "";
  window.$aivo &&
    window.$aivo.ready(() => {
      aivoStaticFilesByRegionMapping.forEach((item) => {
        if (item.countries.includes(country)) {
          token = item.token;
        }
      });
      window.$aivo.registerTokenForExternalZendesk(token);
    });
  window.zESettings = {
    webWidget: {
      position: { horizontal: "left", vertical: "bottom" }
    }
  };
};

const openAivoWhenReady = () => {
  let token = "";
  const country = getBotCountryCode();

  if (typeof $aivo === "undefined" || typeof $aivo.chat === "undefined") {
    setTimeout(openAivoWhenReady, 100);
  } else {
    if ($aivo && $aivo.chat) {
      $aivo.ready(() => {
        aivoStaticFilesByRegionMapping.forEach((item) => {
          if (item.countries.includes(country)) {
            token = item.token;
          }
        });

        $aivo.chat.open();
        $aivo.chat.setHeaderZendesk(Drupal.t("Leave live chat"));
        $aivo.registerTokenForExternalZendesk(token);
      });
    }

    // This is for Medalia
    if ($aivo && $aivo.chat) {
      $aivo.chat.onMinimizeWindow(function () {
        window.chatEnded = true;
        try {
          KAMPYLE_ONSITE_SDK && KAMPYLE_ONSITE_SDK.updatePageView();
        } catch (e) {
          console.warn("KAMPYLE_ONSITE_SDK is not defined;");
        }
      });
      $aivo.chat.onCloseWindow(function () {
        window.chatEnded = true;
        try {
          KAMPYLE_ONSITE_SDK && KAMPYLE_ONSITE_SDK.updatePageView();
        } catch (e) {
          console.warn("KAMPYLE_ONSITE_SDK is not defined;");
        }
      });
    }
  }
};

const getScript = (source) => {
  window._liveChatScriptState = CHAT_SCRIPT_LOADING;
  let script = document.createElement("script");
  const prior = document.getElementsByTagName("script")[0];
  script.async = 1;

  script.onload = (_, isAbort) => {
    window._liveChatScriptState = CHAT_SCRIPT_READY;
  };

  script.src = source;
  prior.parentNode.insertBefore(script, prior);
};

const zEIsAvailable = (country) => true;

const getBotCountryCode = () =>
  drupalSettings.live_chat.is_chinese
    ? "ZH"
    : (window.GEOIP && window.GEOIP.country) || "CA";

const getBotLang = () => {
  // TODO pass from global var (SITE.language)
  const language = "en";

  let lang = drupalSettings.live_chat.is_chinese
    ? drupalSettings.live_chat.lang
    : language;
  if (lang === "zh-hans") {
    lang = "zh";
  } else if (lang === "zh-hant") {
    lang = "zh-tw";
  }

  return lang;
};

