const loadzendeskInitialScript = () => {
  if (isLoaded) {
    return;
  }
  window._liveChatScriptState = CHAT_SCRIPT_LOADING;

  const script = document.createElement("script");
  script.type = "text/javascript";
  script.id = "ze-snippet";
  script.async = true;
  script.src =
    "https://static.zdassets.com/ekr/snippet.js?key=f4eb46e6-3f73-43a5-aa24-5aced02faa51";

  document.getElementsByTagName("head")[0].appendChild(script);

  isLoaded = true;
  zEInitialSettings();
  window.zE && window.zE("webWidget", "hide");
};

let isLoaded = false;
let isConnected = false;

const initzE = (config, country, siteLang) => {
  const isHongKong = country === "HK";
  const isKenya = country === "KE";

  // user tags array
  const user_tags = ["global", "default"];
  const user_tags_hk = ["hong_kong"];
  const user_tags_en = ["english_please"];

  // departments array
  let departments_to_show_global = [];
  let departments_to_show_hk = [];
  let departments_to_show_en = [];

  // loop through configuration to get array of departments that contain tags from the current user
  for (var i = 0; i < config.show_per_tags.length; i++) {
    var item = config.show_per_tags[i];
    if (isHongKong && ulils.intersection(user_tags_hk, item.tags).length) {
      departments_to_show_hk = departments_to_show_hk.concat(
        item.departments_to_show
      );
      updateSettingszE(departments_to_show_hk, ["Hong Kong"]);
    } else if (isKenya && ulils.intersection(user_tags_en, item.tags).length) {
      departments_to_show_en = departments_to_show_en.concat(
        item.departments_to_show
      );
      updateSettingszE(departments_to_show_en, [
        "Please speak to me in English"
      ]);
    } else if (
      siteLang === "zh" &&
      ulils.intersection(user_tags, item.tags).length
    ) {
      departments_to_show_global = departments_to_show_global.concat(
        item.departments_to_show
      );
      window.zE("webWidget", "setLocale", "zh-hans");
      updateSettingszE(departments_to_show_global, [""]);
    } else if (
      siteLang === "zh-tw" &&
      ulils.intersection(user_tags, item.tags).length
    ) {
      departments_to_show_global = departments_to_show_global.concat(
        item.departments_to_show
      );
      window.zE("webWidget", "setLocale", "zh-tw");
      updateSettingszE(departments_to_show_global, [""]);
    } else if (ulils.intersection(user_tags, item.tags).length) {
      departments_to_show_global = departments_to_show_global.concat(
        item.departments_to_show
      );
      updateSettingszE(departments_to_show_global, ["Please select language"]);
    }
  }

  isLoaded = true;
  isConnected = true;
  connectzE();
};

const connectzE = () => {
  zE("webWidget:on", "chat:connected", function () {
    console.log("successfully connected to Zendesk Chat!");
    zE.activate({ hideOnClose: true });
  });
  if (window.zE && "undefined" !== typeof zE.activate) {
    setTimeout(() => {
      return zE.activate({ hideOnClose: true });
    });
    return;
  }
};

const updateSettingszE = (departments, selectOption = "") => {
  window.zE("webWidget", "updateSettings", {
    webWidget: {
      chat: {
        departments: {
          enabled: departments,
          select: selectOption ? selectOption : ""
        }
      }
    }
  });
};

const openzE = (config, country, siteLang) => {
  return isConnected ? connectzE() : initzE(config, country, siteLang);
};

