(function () {
  function openBot(proactive) {
    console.log("connected is ", isConnected);
    //clicked = true;
    if (isConnected || window.zE) {
      connectzE();
    } else {
      openChatBot(proactive);
    }
  }

  window.onZopimChatOpen = (proactive) => {
    openBot(proactive);
  };

  const chatIcon = document.querySelector("a.text-area-account_chat");
  if (chatIcon) {
    chatIcon.addEventListener("click", function (e) {
      e.preventDefault();
      try {
        openChatBot();
      } catch (e) {
        console.warn("LiveChat error");
      }
    });
  }

  if (window.zE) {
    zE("webWidget:on", "chat:end", function () {
      window.chatEnded = true;
      try {
        KAMPYLE_ONSITE_SDK && KAMPYLE_ONSITE_SDK.updatePageView();
      } catch (e) {
        console.warn("KAMPYLE_ONSITE_SDK is not defined;");
      }
    });

    zE("webWidget", "chat:end", function () {
      window.chatEnded = true;
      try {
        KAMPYLE_ONSITE_SDK && KAMPYLE_ONSITE_SDK.updatePageView();
      } catch (e) {
        console.warn("KAMPYLE_ONSITE_SDK is not defined;");
      }
    });

    zE.hide(function () {
      window.chatEnded = true;
      try {
        KAMPYLE_ONSITE_SDK && KAMPYLE_ONSITE_SDK.updatePageView();
      } catch (e) {
        console.warn("KAMPYLE_ONSITE_SDK is not defined;");
      }
    });
  }
})();
