import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
//  element 
import ElementUI from "element-ui";

//  element 
import "element-ui/lib/theme-chalk/index.css";
Vue.directive("back-top", {
  inserted(el, binding) {
    let e = binding.arg || "click";
    el.addEventListener(e, function () {
      //console.log(document.body.scrollTop)
      var cancelScroll = false;
      var timer = null;
      if (cancelScroll === false) {
        timer = setInterval(function () {
          var osTop =
            document.documentElement.scrollTop || document.body.scrollTop;
          //console.log(document.documentElement.scrollTop,document.body.scrollTop)
          var ispeed = Math.floor(osTop - 60);
          document.documentElement.scrollTop = document.body.scrollTop = ispeed;
          if (osTop === 0) {
            clearInterval(timer);
          }
        }, 10);
      } else {
        clearInterval(timer);
        cancelScroll = true;
      }
    });
  }
});
//  element 
Vue.use(ElementUI);

Vue.config.productionTip = false;

// bug/
router.afterEach((to, from, next) => {
  document
    .querySelector("body")
    .setAttribute("style", "overflow: auto !important;");
});
new Vue({
  router,
  store,
  render: (h) => h(App)
}).$mount("#app");
